import { Helmet } from 'react-helmet-async';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { t } from '../../helpers/i18n/dictionary';
import useAuth, { RETURN_URL_PARAM } from '../../hooks/auth/useAuth';
import ActionButton from '../../components/ui/Buttons/ActionButton';
import CharacterHighlighter from '../../components/ui/CharacterHighlighter/CharacterHighlighter';
import { MinimalLayout } from '../../components/ui/Layout/Layout';
import { useEffect } from 'react';
import Loader from '../../components/ui/Icons/Loader/Loader';

const YouAuthLogin = () => {
  const [searchParams] = useSearchParams();
  const { createHomeToken } = useAuth();

  const navigate = useNavigate();
  const cancel = () => {
    navigate(-1);
  };

  const returnUrl = searchParams.get(RETURN_URL_PARAM);
  const targetDomain = decodeURIComponent(returnUrl);

  const doRegisterSite = () => {
    createHomeToken(targetDomain);
  };

  const strippedTarget = targetDomain.replace(new RegExp('^(http|https)://'), '').split('/')[0];

  useEffect(() => {
    if (strippedTarget === window.location.host) {
      doRegisterSite();
    }
  }, [strippedTarget, doRegisterSite]);

  if (strippedTarget === window.location.host) {
    return (
      <MinimalLayout>
        <div className="h-screen">
          <div className="mx-auto flex min-h-full w-full max-w-3xl flex-col p-5">
            <div className="my-auto flex flex-col">
              <Loader className="mx-auto mb-10 h-20 w-20" />
              <div className="text-center">{t('Configuring...')}</div>
            </div>
          </div>
        </div>
      </MinimalLayout>
    );
    //   return (
    //     <>
    //       <Helmet>
    //         <title>{t('Logged on')} | Owner console</title>
    //       </Helmet>
    //       <MinimalLayout>
    //         <section className="my-20">
    //           <div className="container mx-auto p-5">
    //             <div className="max-w-[35rem]">
    //               <h1 className="mb-5 text-4xl dark:text-white">{t('Logged on')}</h1>
    //               <p>
    //                 {t('You have successfully logged on as')} {strippedTarget} {t('on')}{' '}
    //                 {strippedTarget}.{' '}
    //                 {t('Would you like to open your owner console? Or get back to your home page?')}'
    //               </p>

    //               <div className="-sm:mx-2 mt-10 sm:flex sm:flex-row-reverse">
    //                 <ActionButton
    //                   onClick={doRegisterSite}
    //                   className="my-3 block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none sm:mx-2 sm:my-auto"
    //                 >
    //                   {t('Open my Home Page')}
    //                 </ActionButton>
    //                 <ActionButton
    //                   className="my-3 sm:mx-2 sm:my-auto"
    //                   type="secondary"
    //                   onClick={() => navigate('/owner')}
    //                 >
    //                   {t('Open my Owner console')}
    //                 </ActionButton>
    //               </div>
    //             </div>
    //           </div>
    //         </section>
    //       </MinimalLayout>
    //     </>
    //   );
  }

  return (
    <>
      <Helmet>
        <title>{t('Register')} | Owner console</title>
      </Helmet>
      <MinimalLayout>
        <section className="my-20">
          <div className="container mx-auto p-5">
            <div className="max-w-[35rem]">
              <h1 className="mb-5 text-4xl dark:text-white">
                {t('Authorize')} "<CharacterHighlighter>{strippedTarget}</CharacterHighlighter>"
              </h1>
              <p>
                "<CharacterHighlighter>{strippedTarget}</CharacterHighlighter>"{' '}
                {t('is not authorized to verify identity.')}
              </p>
              <p className="mt-2">
                {t('By authorizing')} "<CharacterHighlighter>{strippedTarget}</CharacterHighlighter>
                " {t('you allow it to verify your identity')}
              </p>
              <div className="-sm:mx-2 mt-10 sm:flex sm:flex-row-reverse">
                <ActionButton
                  onClick={doRegisterSite}
                  className="my-3 block rounded border-0 bg-green-500 py-2 px-4 text-white hover:bg-green-600 focus:outline-none sm:mx-2 sm:my-auto"
                >
                  {t('Authorize')}
                </ActionButton>
                <ActionButton className="my-3 sm:mx-2 sm:my-auto" type="secondary" onClick={cancel}>
                  {t('Cancel')}
                </ActionButton>
              </div>
            </div>
          </div>
        </section>
      </MinimalLayout>
    </>
  );
};

export default YouAuthLogin;
